<template>
  <div :class="['z-checkboxs z-theme-default']">
    <div :class="[row ? 'z-flex' : '']">
      <div
        v-for="(item, index) of items" 
        :key="`z_checkbox_${ index }`"
      >
        <v-checkbox
          v-model="values"
          :label="item.label"
          :value="item.value"
          hide-details
          class="mr-4"
        ></v-checkbox>
      </div>
    </div>
    <p class="z-error" v-if="errorMessage">{{ errorMessage }}</p>
  </div>
</template>

<script>
  import FormMixins from '../mixins/form'
  import FormValidatorMixins from '../mixins/validator'

  export default {
    name: 'ZCheckboxs',
    mixins: [FormMixins, FormValidatorMixins],
    props: {
      options: {
        type: Array,
        required: true
      },
      // column: {
      //   type: Boolean,
      //   default: false
      // },
      row: {
        type: Boolean,
        default: true
      },
      defaultValue: {
        type: Array,
        default: () => { return [] }
      }
    },

    data() {
      return {
        values: [],
        items: []
      }
    },

    created() {
      for(let item of this.options) {
        this.items.push({
          label: item.label,
          value: item.value
        })
      }
    },

    watch: {
      defaultValue: {
        handler(value) {
          this.values = value
        },
        immediate: true
      },

      values: {
        handler(values) {
          if(values.length > 0) {
            this.value = values
          }
        },
        immediate: true
      },

      value: {
        handler(value) {
          if(!value) {
            this.values = []
          }else {
            this.values = value
          }
        },
        immediate: true
      }
    }
  }
</script>

<style scoped lang="css">
  @import url("../styles/styles.css");
</style>